@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}


:root {
  --font-family: 'Manrope', sans-serif;

  --gradient-text: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);
  --gradient-bar: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%);
  
  --color-bg: #040C18;
  --color-footer : #031B34;
  --color-blog: #042c54;
  --color-text: #81AFDD;
  --color-subtext: #FF8A71;
}
.navbar {
    background-color: rgba(33, 37, 41, 255);
    /* margin-top: -80px; */
    z-index: 10;
    position: fixed;
    width: 100%;
    height: 80px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
  
  .logo {
    justify-self: start;
    margin-left: 2rem;
    font-size: 2rem;
    /* background-color: yellow; */
  }
  
  .logo a {
    color: #f5f5f5;
    text-decoration: none;
  }
  
  .right-itm {
    justify-self: end;
    color: #f5f5f5;
    margin-right: 2rem;
    /* background-color: #fff; */
  }
  
  .mobile-view {
    display: grid;
    grid-template-rows: 15% 15% 15% 15%;
    align-items: center;
    background-color: rgba(33, 37, 41, 255);
    /* background-color: yellow; */
    padding: 2rem;
    list-style: none;
    position: fixed;
    transition: 350ms;
    top: 80px;
    left: -120%;
    width: 100vw;
    height: 100vh;
  }
  
  .active {
    left: 0px;
    transition: 450ms;
  }
  
  .nav-itm {
    /* background-color: #fff; */
    display: flex;
    /* border-radius: 4px; */
    height: 65px;
    /* align-items: center; */
  }
  
  .nav-itm a {
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    border-radius: 4px;
    color: #f5f5f5;
  }
  
  .nav-itm a:hover {
    background-color: #1a83ff;
  }
  
  span {
    margin-right: 16px;
  }
  
  .desktop {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    align-items: center;
    grid-gap: 1.3rem;
    gap: 1.3rem;
  }
  
  .nav-d-itm {
    list-style: none;
  }
  
  .nav-d-itm a {
    text-decoration: none;
    display: flex;
    align-items: center;
    list-style: none;
    font-size: 1.1rem;
    border-radius: 4px;
    padding: 5px 6px;
    color: #f5f5f5;
  }
  
  .nav-d-itm a:hover {
    background-color: #1a83ff;
  }
  
  @media only Screen and (max-width: 750px) {
    .desktop {
      display: none;
    }
  }
  
  @media only Screen and (min-width: 750px) {
    .mobile {
      display: none;
    }
    .navbar {
      grid-template-columns: 1fr 2fr;
    }
  }
  
.footer {
  background-color: rgba(33, 37, 41, 255);
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  padding: 4rem;
  list-style: none;
  grid-row-gap: 4rem;
}

.footer-title {
  display: grid;
  justify-content: center;
  /* background-color: pink; */
  list-style: none;
  grid-gap: 0.9rem;
  gap: 0.9rem;
}

.footer-sub-title a {
  text-decoration: none;
  color: #f5f5f5;
  display: flex;
}

.footer-sub-title a span {
  padding-right: 7px;
}

.main-title {
  font-size: 1.2rem;
  font-weight: bold;
  color: #f5f5f5;
  margin-bottom: 1.4rem;
  text-align: center;
}

@media only Screen and (max-width: 850px) {
  .footer {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media only Screen and (max-width: 500px) {
  .footer {
    grid-template-columns: repeat(1, 1fr);
  }
}

